import * as React from 'react'
import { StaticImage } from 'gatsby-plugin-image'

const SideArticle = ({data}) => {

  return(
      <>
      <div className='card article-card mb-4'>
      <nav id="scrollspy1" className="navbar scroll-background flex-column align-items-stretch p-3">
        <a className="navbar-brand toc-title" href="#">Table Of Content</a>
        <nav className="nav nav-pills flex-column">
        {
          data.mdx.tableOfContents.items.map(element => {                    
            
            if(element.items !== undefined) {
              let sub = element.items.map (subelement => {
                return(
                    <a className="nav-link ms-3 my-1" href={subelement.url}>{subelement.title}</a>                            
                )
              });

              return(<>
                <a className="nav-link" href={element.url}>{element.title}</a>
                <nav className="nav nav-pills flex-column">
                {sub}
                </nav>
              </>);
            }
            else {
              return(<>
                <a className="nav-link" href={element.url}>{element.title}</a>
              </>);
            }
            
          })
        }
        </nav>
      </nav>
      </div>
      <div className='card article-card mb-4'>
        <div class="card-body">
          <center><h6 class="card-title toc-title">ABOUT ME</h6></center>
          <StaticImage alt='logo' src='../images/me.png' className='about-me-article-img'/>
          <p class="card-text card-article-text mt-2">I love doing absolutely everything rather than studying.</p>
        </div>
      </div>
      <iframe className='mb-4' src={data.mdx.frontmatter.music} width="100%" height="152" allowfullscreen="" allow="autoplay; clipboard-write; encrypted-media; fullscreen; picture-in-picture" loading="lazy"></iframe>
      </>
  );

}

export default SideArticle;